import React from "react";

import { Link } from "gatsby";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import Container from "react-bootstrap/Container";
import resume from "../Resume.pdf";
import "bootstrap/dist/css/bootstrap.min.css";
import "../App.css";

const DottedWave = () => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    viewBox="0 0 1440 100" // Adjusted height
    style={{ display: 'block', width: '100%', height: 'auto', marginTop: '-15px' }} // Keep negative margin for overlap
  >
    <path
      d="M0,60 C480,10 960,160 1440,60" // Adjusted path to stay within view
      fill="none"
      stroke="black"
      strokeWidth="1" // Increased stroke width for visibility
      strokeDasharray="40, 20" // Dotted line effect
    />
  </svg>
);

const Navigation = () => {
  return (
    <>
      <Navbar className="navbar-custom" bg="transparent" expand="lg">
        <Container style={{ position: 'relative' }}>
          <Navbar.Brand as={Link} to="/">
            Emery Li
          </Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="ms-auto">
              <Nav.Link as={Link} to="/" activeClassName="active-link">
                Home
              </Nav.Link>
              <Nav.Link as={Link} to="/projects" activeClassName="active-link">
                Projects
              </Nav.Link>
              <Nav.Link href={resume} target="_blank">
                Resume
              </Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <DottedWave style={{ position: 'absolute', top: '100%', left: 0 }}/>
    </>
  );
};

export default Navigation;